<template>
  <v-main class="pa-0">
    <v-card class="mx-auto text-center mb-6" width="30%" flat style="background-color: transparent">
      <span class="mx-8 text-h5">Area: {{ Area }}</span>
      <span class="mx-8 text-h5">Position: {{ Position }}</span>
    </v-card>

    <v-card class="mx-auto text-center mb-12" width="80%" flat style="background-color: transparent">
      <v-simple-table class="mb-6" style="background-color: transparent; border: 0px solid #eee;">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-center text-h6 green--text text--accent-3">Graphics Card</th>
              <th class="text-center text-h6 green--text text--accent-3">Electricity</th>
              <th class="text-center text-h6 green--text text--accent-3">Output Value</th>
              <th class="text-center text-h6 green--text text--accent-3">Price</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in Card" :key="item.name">
              <td>{{ item.Graphics_Name }}</td>
              <td>{{ item.Electricity }}</td>
              <td>{{ item.Output_Value }}</td>
              <td>{{ item.Price }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <th class="text-center text-h4 green--text text--accent-3">Total</th>
              <th class="text-center text-h6 green--text text--accent-3">{{ TotalElectricity }}</th>
              <th class="text-center text-h6 green--text text--accent-3">{{ TotalOutput_Value }}</th>
              <th class="text-center text-h6 green--text text--accent-3">{{ TotalPrice }}</th>
            </tr>
          </tfoot>
        </template>
      </v-simple-table>
      <span class="yellow--text text-h5 mx-auto">Total Price: {{ TotalPrice }} USDT</span>
    </v-card>
  </v-main>
</template>

<script>
export default {
  name: "checkOrder",
  props: [
    "Area",
    "Position",
    "Card",
    "TotalElectricity",
    "TotalOutput_Value",
    "TotalPrice",
  ],
  data() {
    return {};
  },
};
</script>