<template>
  <v-main
    style="background: linear-gradient(to right, #002832 0%, #000032 100%)"
  >
    <div class="pt-12"></div>
    <v-card
      class="mx-auto"
      width="90%"
      outlined
      elevation="0"
      style="background-color: transparent"
    >
      <v-stepper
        v-model="e1"
        dark
        alt-labels
        style="background-color: transparent"
      >
        <v-stepper-header>
          <v-stepper-step complete-icon="mdi-check" :complete="e1 > 1" step="1">
            Choose a seat
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step complete-icon="mdi-check" :complete="e1 > 2" step="2">
            Choose a device
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step complete-icon="mdi-check" step="3">
            Check your order
          </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content class="text-md-start text-center" step="1">
            <Position @postPositionValue="getPositionValue" />

            <v-card-actions>
              <v-btn
                class="ml-auto"
                color="primary"
                :disabled="positionNextModal"
                :loading="positionNextModal"
                @click="nextChoseCard()"
              >
                Continue
              </v-btn>
            </v-card-actions>
          </v-stepper-content>

          <v-stepper-content step="2">
            <GraphicsChose @postCardValue="getCardValue" />

            <v-card-actions>
              <div class="ml-auto">
                <v-btn
                  color="primary"
                  class="mr-8"
                  :disabled="cardNextModal"
                  :loading="cardNextModal"
                  @click="nextCheckOrder()"
                >
                  Continue
                </v-btn>
                <v-btn color="red" @click="e1 = 1"> Cancel </v-btn>
              </div>
            </v-card-actions>
          </v-stepper-content>

          <v-stepper-content step="3">
            <CheckOrder
              :Area="getChoseArea"
              :Position="getChosePosition"
              :Card="getCard"
              :TotalElectricity="totalElectricity"
              :TotalOutput_Value="totalOutput_Value"
              :TotalPrice="totalPrice"
            />
            <v-card-actions>
              <div class="mx-auto">
                <v-btn color="primary mr-12" @click="Checkout()">
                  Continue
                </v-btn>
                <v-btn color="red" @click="e1 = 2"> Cancel </v-btn>
              </div>
            </v-card-actions>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-card>
    <div class="pt-12"></div>
    <sweet-modal ref="cantCheckout" icon="warning">
      Your account balance is insufficient.
      <v-btn slot="button" class="mr-5" color="primary">Go to deposit</v-btn>
      <v-btn slot="button" color="primary" @click="$refs.cantCheckout.close()"
        >Cancel</v-btn
      >
    </sweet-modal>

    <div class="text-center">
      <v-dialog v-model="canCheckoutModal" width="600">
        <v-card class="text-center" style="background: black">
          <div class="pt-16"></div>
          <v-img
            class="mx-auto"
            src="@/assets/images/shop car_icon.png"
            max-width="50%"
          />
          <span class="white--text text-h6"
            >Are you sure to purchase this merchandise?</span
          >
          <div class="pt-12"></div>
          <v-card-actions>
            <div class="mx-auto">
              <v-btn class="ml-12" color="primary" @click="deduction()">
                Sure
              </v-btn>
              <v-btn
                class="ml-10"
                color="error"
                @click="canCheckoutModal = false"
              >
                Cancel
              </v-btn>
            </div>
          </v-card-actions>
          <div class="pt-12"></div>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="cantCheckoutModal" width="600">
        <v-card class="text-center" style="background: black">
          <div class="pt-16"></div>
          <v-img
            class="mx-auto"
            src="@/assets/images/shop car_icon.png"
            max-width="50%"
          />
          <span class="white--text text-h6"
            >Your account balance is insufficient.</span
          >
          <div class="pt-12"></div>
          <v-card-actions>
            <div class="mx-auto">
              <v-btn class="ml-12" color="primary"> Deposit </v-btn>
              <v-btn
                class="ml-10"
                color="error"
                @click="cantCheckoutModal = false"
              >
                Cancel
              </v-btn>
            </div>
          </v-card-actions>
          <div class="pt-12"></div>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="settlementCompleteModal" width="600">
        <v-card class="text-center" style="background: black">
          <div class="pt-16"></div>
          <v-img
            class="mx-auto"
            src="@/assets/images/thanks_icon.png"
            max-width="50%"
          />
          <span class="white--text text-h6">Thank you for your purchase</span>
          <div class="pt-12"></div>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog class="div-field" v-model="positionNextModal" hide-overlay persistent>
        <v-card class="text-center error-card d-flex align-center" width="auto" height="100">
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Please select a seat first
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog class="div-field" v-model="cardNextModal" hide-overlay persistent width="30%">
        <v-card class="text-center error-card d-flex align-center" width="auto" height="100">
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Please select a graphics card first
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </v-main>
</template>

<script>
import { userBalance, selectPosition } from "../api";
import Position from "./Selection.vue";
import GraphicsChose from "./GraphicsChose.vue";
import CheckOrder from "./checkOrder.vue";

export default {
  name: "PurchaseSteps",
  data() {
    return {
      e1: 1, //tab頁數
      getChoseArea: "", //選擇位置子組件選取的地區值
      postChoseArea: "", //確認購買後給api送出去的地區值
      getChosePosition: "", //選擇位置子組件選取的位置號碼值
      getCard: "", //選擇顯卡子組件選取的顯卡資料列表
      totalElectricity: "", //選取的顯卡資料列表內Electricity的總值
      totalOutput_Value: "", //選取的顯卡資料列表內Output_Value的總值
      totalPrice: "", //選取的顯卡資料列表內Price的總值
      canCheckoutModal: false,
      cantCheckoutModal: false,
      settlementCompleteModal: false,
      positionNextModal: false,
      cardNextModal: false,
    };
  },
  components: {
    Position,
    GraphicsChose,
    CheckOrder,
  },
  created() {
    this.$store.dispatch("getUserData");
  },
  computed: {
    Email() {
      return this.$store.state.email;
    },
  },
  methods: {
    //取得選擇位置子組件傳過來的值
    getPositionValue(choseArea, chosePosition) {
      this.getChoseArea = choseArea;
      this.postChoseArea = choseArea;
      this.getChosePosition = chosePosition;
    },

    //取得選擇顯卡資訊子組件傳過來的值
    getCardValue(choseCard) {
      this.getCard = choseCard;
    },

    //前往選擇顯示卡
    nextChoseCard() {
      if (this.getChosePosition == "") {
        this.positionNextModal = true;
        setTimeout(() => (this.positionNextModal = false), 2000);
      } else {
        this.e1 = 2;
      }
    },

    //前往察看訂單資料
    nextCheckOrder() {
      if (this.getCard.length < 8) {
        this.cardNextModal = true;
        setTimeout(() => (this.cardNextModal = false), 2000);
      } else {
        this.getChoseArea = String.fromCharCode(this.getChoseArea + 65); //將區域數字轉成區域字母
        this.TotalCard();
        this.e1 = 3;
      }
    },

    //結帳處理
    Checkout() {
      userBalance()
        .then((result) => {
          if (result.data.Sum_Amt < this.totalPrice) {
            this.cantCheckoutModal = true;
          } else {
            this.canCheckoutModal = true;
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //確認結帳後執行購買選位的Api
    deduction() {
      this.canCheckoutModal = false;
      const cardId1 = this.getCard[0].id;
      const cardId2 = this.getCard[1].id;
      const cardId3 = this.getCard[2].id;
      const cardId4 = this.getCard[3].id;
      const cardId5 = this.getCard[4].id;
      const cardId6 = this.getCard[5].id;
      const cardId7 = this.getCard[6].id;
      const cardId8 = this.getCard[7].id;

      var params = new URLSearchParams();
      params.append("Area", this.postChoseArea);
      params.append("PositionId", this.getChosePosition);
      params.append("DId1", cardId1);
      params.append("DId2", cardId2);
      params.append("DId3", cardId3);
      params.append("DId4", cardId4);
      params.append("DId5", cardId5);
      params.append("DId6", cardId6);
      params.append("DId7", cardId7);
      params.append("DId8", cardId8);

      selectPosition(params)
        .then((result) => {
          if (result.data.success == true) {
            this.settlementCompleteModal = true;
            setTimeout(function () {
              this.$router.push({ name: "Home" });
            }, 4000);
          } else {
            console.log(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //計算選取顯卡的total電量、產值、價錢
    TotalCard() {
      let totalElectricity = 0;
      let totalOutput_Value = 0;
      let totalPrice = 0;

      for (var index = 0; index < this.getCard.length; index++) {
        totalElectricity += parseInt(this.getCard[index].Electricity);
        totalOutput_Value += parseInt(this.getCard[index].Output_Value);
        totalPrice += parseInt(this.getCard[index].Price);
      }

      this.totalElectricity = totalElectricity;
      this.totalOutput_Value = totalOutput_Value;
      this.totalPrice = totalPrice;
    },
  },
};
</script>

<style lang="scss" scoped>
.error-card {
  background-color: #000000;
  opacity: 0.9;
}

.div-field {
  width: 30%;
}

@media screen and (max-width: 600px) {
  .div-field {
    width: 100%;
  }
}
</style>