<template>
  <v-main class="pa-0">
    <v-card flat style="background-color: transparent">
      <v-tabs
        v-model="tab"
        centered
        @change="getArea()"
        style="background: linear-gradient(to right, #002832 0%, #000032 100%)"
      >
        <v-tab> A </v-tab>

        <v-tab> B </v-tab>

        <v-tab> C </v-tab>
      </v-tabs>
      <div class="pt-8"></div>
      <v-tabs-items v-model="tab" style="background-color: transparent">
        <v-tab-item v-for="i in 3" :key="i" :value="i - 1">
          <v-card
            class="mx-auto text-center"
            width="90%"
            flat
            style="background-color: transparent"
          >
            <ul
              class="d-inline-block d-md-inline-flex pt-8 pt-md-0 pl-0"
              v-for="item in list"
              :key="item.name"
            >
              <li
                class="text-center mx-2"
                v-for="seat in item"
                :key="seat.name"
              >
                <v-btn
                  outlined
                  dark
                  text
                  :class="seletClass(seat)"
                  @click="chose(seat)"
                  >{{ seat.position }}</v-btn
                >
              </li>
            </ul>
            <div class="pt-5"></div>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
      <div class="pt-12"></div>
    </v-card>
    <div class="text-center">
      <v-dialog v-model="cardControlModal" width="500">
        <v-card class="text-center" style="background: black">
          <div class="pt-8"></div>
          <div class="pt-8"></div>
          <v-simple-table dark style="background: black">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center green--text text--accent-3">
                    Graphics Name
                  </th>
                  <th class="text-center green--text text--accent-3">Status</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  class="text-center"
                  v-for="item in usedList"
                  :key="item.name"
                >
                  <td>{{ item.Graphics_Name }}</td>
                  <td>{{ item.IsUAllow }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <div class="pt-6"></div>
          <span class="text-h6 mx-auto"
            >Total Output: {{ totalOutPutValue }}</span
          >

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn large color="white" text @click="cardControlModal = false">
              Close
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </v-main>
</template>

<script>
import { positionStatus, positionData } from "../api";

export default {
  name: "selectoin",
  data: () => ({
    isdas: false, //判断按钮是否锁定
    list: "", //後端傳來位置的資料
    tab: 0, //選項卡
    choseArea: "", // 選擇的區域 0:A區, 1:B區 ....以此類推
    chosePosition: "", //選擇的位置
    dialog: false, //查看被買走座位資訊
    usedList: "", //使用中位置的設備資料
    totalOutPutValue: "", //位置選購設備的總產值
    deviceStatus: "", //設備狀態
    cardControlModal: false,
    isChange: -1,
  }),
  created() {
    //call顯示位置的api
    var params = new URLSearchParams();
    params.append("Area", this.tab);

    positionStatus(params)
      .then((result) => {
        this.list = result.data.list;
      })
      .catch((err) => {
        console.error(err);
      });
  },
  methods: {
    //點擊單獨選取座位
    chose(seat) {
      if (seat.use == 1) {
        this.viewDevice(seat);
        this.cardControlModal = true;
      } else if (seat.bad == 1) {
        alert("此位置正在維修中");
      } else {
        if (seat.position != this.isChange) {
          this.isChange = seat.position;
          this.choseArea = this.tab;
          this.chosePosition = seat.position;
          this.$emit("postPositionValue", this.choseArea, this.chosePosition);
        } else {
          this.isChange = -1;
          this.choseArea = "";
          this.chosePosition = "";
          this.$emit("postPositionValue", this.choseArea, this.chosePosition);
        }
      }
    },

    //位置狀態的顏色
    seletClass(seat) {
      //樣式
      if (seat.use && seat.bad == 1) {
        return "someBad";
      } else if (seat.bad == 1) {
        return "bad";
      } else if (seat.use == 1) {
        return "used";
      }

      if (this.isChange == seat.position) {
        return "select";
      }
    },

    //選項卡去區分區域所要顯示的位置
    getArea() {
      var params = new URLSearchParams();
      params.append("Area", this.tab);

      positionStatus(params)
        .then((result) => {
          this.list = result.data.list;
        })
        .catch((err) => {
          console.error(err);
        });

      this.isChange = -1;
      this.choseArea = "";
      this.chosePosition = "";
      this.$emit("postPositionValue", this.choseArea, this.chosePosition);
    },

    //查看使用中的位置所購買的設備
    viewDevice(seat) {
      var data = new URLSearchParams();
      data.append("Area", this.tab);
      data.append("PositionId", seat.position);

      positionData(data)
        .then((result) => {
          this.usedList = result.data.list;
          this.totalOutPutValue = result.data.Total_ALL;
          this.usedList.forEach((element) => {
            this.deviceStatusDisplay(element);
          });
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //顯示使用座位上顯卡的狀態
    deviceStatusDisplay(element) {
      if (element.IsUAllow == 1) {
        element.IsUAllow = "Normal";
        return element.IsUAllow;
      } else {
        element.IsUAllow = "Breakdown";
        return element.IsUAllow;
      }
    },
  },
};
</script>

<style type="text/css">
.background {
  background-color: red;
}
.flex {
  display: flex;
}
.seat {
  width: 30px;
  height: 30px;
  list-style: none;
  border: 1px solid #eee;
}
ul li {
  width: auto;
  height: auto;
  list-style: none;
  border: 1px solid #eee;
  margin: 1% auto;
  padding: 0;
}
.status {
  color: red;
}
.used {
  background-color: #00aa78;
}
.select {
  background-color: #a05601;
}

.bad {
  background-color: rgb(63, 0, 0);
}
.someBad {
  background-color: #9999;
}

.v-slide-group__content {
  background: linear-gradient(to right, #002832 0%, #000032 100%);
}
</style>