<template>
  <v-main class="pa-0">
    <v-card class="text-center" flat style="background-color: transparent">
      <div class="pt-6"></div>
      <v-row class="text-start text-md-center">
        <v-col class="pb-6" cols="12" md="4">
          <v-btn class="mr-8" color="primary" @click="clearChose()"
            >Reselect</v-btn
          >
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field
            v-model="search"
            placeholder="Search..."
            append-icon="fas fa-search"
            dense
            outlined
            @click:append="searchCardList()"
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="4">
          <span>You select the card number: {{ addnumber }}</span>
        </v-col>
      </v-row>
      <v-simple-table dark style="background-color: transparent">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-center text-h6 green--text text--accent-3">
                Graphics Card
              </th>
              <th class="text-center text-h6 green--text text--accent-3">
                Computing Power
              </th>
              <th class="text-center text-h6 green--text text--accent-3">
                Electricity
              </th>
              <th class="text-center text-h6 green--text text--accent-3">
                Output Value
              </th>
              <th class="text-center text-h6 green--text text--accent-3">
                Reserve
              </th>
              <th class="text-center text-h6 green--text text--accent-3">
                Price
              </th>
              <th class="text-center text-h6 green--text text--accent-3">
                operate
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in list" :key="item.name">
              <td>{{ item.Graphics_Name }}</td>
              <td>{{ item.Computing_Power }}</td>
              <td>{{ item.Electricity }}</td>
              <td>{{ item.Output_Value }}</td>
              <td>{{ item.Reserve }}</td>
              <td>{{ item.Price }}</td>
              <td>
                <v-btn
                  small
                  v-if="item.Reserve > 0"
                  :disabled="addDisabled"
                  @click="add(item)"
                  style="
                    background: linear-gradient(
                      to bottom,
                      #002832 0%,
                      #000032 100%
                    );
                  "
                  >add</v-btn
                >
                <v-btn
                  small
                  v-if="item.Reserve <= 0"
                  disabled
                  style="
                    background: linear-gradient(
                      to bottom,
                      #002832 0%,
                      #000032 100%
                    );
                  "
                  >add</v-btn
                >
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card>
    <div class="text-center">
      <v-pagination
        v-model="cardPageLen"
        class="my-4"
        prev-icon="mdi-menu-left"
        next-icon="mdi-menu-right"
        :length="totalCardPage"
        :total-visible="8"
        @input="showCardList()"
      ></v-pagination>
    </div>
    <div class="pt-16"></div>
  </v-main>
</template>

<script>
import { selectCardList } from "../api";

export default {
  data() {
    return {
      list: [], //將顯卡api抓出來的資料指進去這個data裡
      addCard: [], //將選取的顯卡加入到這個array
      addDisabled: false, //控制add按鈕的disbaled
      addnumber: 0, //計算已選取的顯卡數
      cardPageLen: 1, //顯卡分頁頁數
      totalCardPage: 1, //顯卡分頁總頁數
      search: "",
    };
  },
  created() {
    var params = new URLSearchParams();
    params.append("A_Field", this.search);
    params.append("page", this.cardPageLen);
    params.append("number", 10);

    selectCardList(params)
      .then((result) => {
        //分頁邏輯處理
        let maxLength = 10;
        this.totalCardPage = Math.ceil(
          parseInt(result.data.number) / maxLength
        );
        for (let index = 0; index <= this.totalCardPage; index++) {
          if (index == 1) {
            this.cardPageLen = index;
            this.showCardList();
            break;
          }
        }
      })
      .catch((err) => {
        console.error(err);
      });
  },
  watch: {
    //監聽如果選擇顯示的數量大於等於8就把添加按鈕關起來
    addCard() {
      if (this.addCard.length >= 8) {
        this.addDisabled = true;
      } else {
        this.addDisabled = false;
      }
    },
  },
  methods: {
    //分頁處理過後秀出顯卡列表
    showCardList() {
      var params = new URLSearchParams();
      params.append("A_Field", this.search);
      params.append("page", this.cardPageLen);
      params.append("number", 10);

      selectCardList(params)
        .then((result) => {
          this.list = result.data.list;
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //搜尋顯卡列表
    searchCardList() {
      var params = new URLSearchParams();
      params.append("A_Field", this.search);
      params.append("page", this.cardPageLen);
      params.append("number", 10);

      selectCardList(params)
        .then((result) => {
          let maxLength = 10;
          this.totalCardPage = Math.ceil(
            parseInt(result.data.number) / maxLength
          );
          for (let index = 0; index <= this.totalCardPage; index++) {
            if (index == 1) {
              this.cardPageLen = index;
              this.showCardList();
              break;
            }
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //添加選擇的顯示卡
    add(chose) {
      this.addCard.push(chose);
      this.$emit("postCardValue", this.addCard);
      this.addnumber++;
      console.log(this.addCard);
      console.log(this.addCard.length);

      chose.Reserve -= 1;
    },

    //清除選擇的顯示卡
    clearChose() {
      this.addCard = [];
      this.addnumber = 0;
      this.showCardList();
      this.$emit("postCardValue", this.addCard);
    },
  },
};
</script>

<style lang="css">
.fl-table {
  border-radius: 5px;
  font-size: 12px;
  font-weight: normal;
  border: none;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  white-space: nowrap;
  background-color: white;
}

.v-pagination {
  width: 0 !important;
}

ul li {
  border: none;
}
</style>